let shopBtns = document.querySelectorAll('.shop__button')
let shopBtnIcons = document.querySelectorAll('.shop__button-icon')
let shopItem = document.querySelectorAll('.shop__item')
let shopMiddle = document.querySelector('.shop__middle')
console.log(shopBtnIcons)

shopBtns.forEach(function(el){
  el.addEventListener('click', function(){
    shopBtns.forEach(function(el){
      el.classList.remove('shop__button--active')
    })
    if(el.classList.contains('shop__button--active')) {
      el.classList.remove('shop__button--active')
    } else {
      el.classList.add('shop__button--active')
    }
  })
})

shopBtnIcons.forEach(function(el){
  el.addEventListener('click', function(){
    shopBtnIcons.forEach(function(el){
      el.classList.remove('shop__button-icon--icons')
    })
    if(el.classList.contains('shop__button-icon--icons')) {
      el.classList.remove('shop__button-icon--icons')

    } else {
      el.classList.add('shop__button-icon--icons')
      if (el.classList.contains('shop__button-icon--line')) {
        shopItem.forEach(function(el){
          el.classList.add('shop__item--line')
        })
        shopMiddle.classList.add('shop__middle--line')
        shopMiddle.classList.remove('shop__middle--grid')
      } else {
        shopItem.forEach(function(el){
          el.classList.remove('shop__item--line')
        })
        shopMiddle.classList.remove('shop__middle--line')
        shopMiddle.classList.add('shop__middle--grid')
      }
    }
  })
})

