let sidebar__btn = document.querySelectorAll('.shop__sidebar-btn')
let sidebar__item = document.querySelectorAll('.shop__sidebar-item')
sidebar__btn.forEach(el => {

  el.addEventListener('click', function(){
    let next = el.nextElementSibling
    if(el.classList.contains('shop__sidebar-btn--active')){
      next.style.height = 0
      el.classList.remove('shop__sidebar-btn--active')
    } else {
      next.style.height = next.scrollHeight + 'px'
      el.classList.add('shop__sidebar-btn--active')
    }

  })
});
