let shopMenu = document.querySelector('.shop__menu')
let shopSubMenu = document.querySelector('.shop__submenu')
let shopSubMenu2 = document.querySelectorAll('.shop__submenu-level2')
let shopSubMenu3 = document.querySelectorAll('.shop__submenu-level3')

shopMenu.addEventListener('click', function(e){
  trgt = e.target
  if(trgt.classList.contains('shop__menu')){
    shopMenu.classList.toggle('shop__menu--active')
    shopSubMenu.classList.toggle('shop__submenu--active')
  }

})

shopSubMenu.addEventListener('click', function(e){
  trgt = e.target
  if(trgt.classList.contains('shop__submenu')){
    shopSubMenu.classList.toggle('shop__submenu--opened')
    shopSubMenu2.forEach(el => {
      el.classList.toggle('shop__submenu-level2--active')
    });
  }
})

shopSubMenu2.forEach(el => {
  el.addEventListener('click', function(e){
    trgt = e.target
    if(trgt.classList.contains('shop__submenu-level2')){
      el.classList.toggle('shop__submenu-level2--opened')
      next = el.lastElementChild.lastElementChild
      next.classList.toggle('shop__submenu-level3--active')

    }
  })
});

