let sidebarBtn = document.querySelector('.sidebar__open-btn')
let sidebar = document.querySelector('.shop__sidebar')
sidebarBtn.addEventListener('click', function(){
  if(sidebarBtn.classList.contains('sidebar__open-btn--active')){
    sidebarBtn.classList.remove('sidebar__open-btn--active')
    sidebar.classList.remove('shop__sidebar--active')
  } else {
    sidebarBtn.classList.add('sidebar__open-btn--active')
    sidebar.classList.add('shop__sidebar--active')
  }
})
